'use client';

import { useEffect } from 'react';

export default function BootstrapClient() {
  useEffect(() => {
    // Importación dinámica con tipos
    const initializeBootstrap = async () => {
      try {
        // Importar Bootstrap
        const bootstrap = await import('bootstrap/dist/js/bootstrap.bundle.min');
        
        // Importar otras librerías
        const [GLightbox, Choices, Dropzone] = await Promise.all([
          import('glightbox'),
          import('choices.js'),
          import('dropzone')
        ]);

        // Inicializar GLightbox
        GLightbox.default({
          selector: '[data-gallery="image-popup"]',
          touchNavigation: true,
          loop: true,
        });

        // Inicializar Choices
        document.querySelectorAll('.js-choice').forEach((select) => {
          new Choices.default(select as HTMLSelectElement, {
            removeItemButton: true,
            shouldSort: false,
          });
        });

        // Inicializar Dropzone
        Dropzone.default.autoDiscover = false;
        document.querySelectorAll('.dropzone').forEach((dropzone) => {
          const maxFiles = (dropzone as HTMLElement).dataset.dropzone;
          new Dropzone.default(dropzone as HTMLElement, {
            url: '/upload',
            maxFiles: maxFiles ? parseInt(maxFiles) : 1,
            acceptedFiles: 'image/*',
          });
        });

      } catch (error) {
        console.error('Error initializing libraries:', error);
      }
    };

    initializeBootstrap();
  }, []);

  return null;
}